input,
textarea {
  padding: 8px;
  border: 2px solid #39364f;
  border-radius: 8px;
  &.input {
    &--error {
      border-color: red;
    }
  }
}

.form {
  margin: 70px 0 35px 0;
  background: white;
  padding: 16px;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.15);
  label {
    margin-bottom: 4px;
  }
  input {
    margin-right: 24px;
    margin-bottom: 24px;
    border-radius: 0.4rem;
    border: 0.2rem solid #dbe0e6;
    background-color: #dbe0e6;
    height: 48px;
  }
  textarea,
  .text--editor {
    margin-bottom: 24px;
  }
  [type="text"],
  [type="email"],
  [type="password"],
  [type="submit"] {
    max-width: 400px;
    font-size: 0.9rem;
  }
  input[type="number"] {
    width: 175px;
  }
  input[type="url"] {
    max-width: 400px;
    font-size: 0.9rem;
  }
  input[type="date"] {
    width: 175px;
  }
}

.card.form__eb {
  margin: 0;
  padding: 28px 5rem;
  .fc {
    flex-direction: row;
    flex-wrap: wrap;
    label {
      flex-basis: 100%;
    }
    input[type="text"] {
      flex-basis: 50%;
      margin: 0;
      margin-right: 16px;
      height: 38px;
      max-width: 267px;
    }
    input[type="submit"] {
      margin-bottom: 0;
      flex-basis: calc(50% - 16px);
      margin: 0 16px 0 0;
      height: 38px;
      max-width: 120px;
    }
  }
}



